* {
  -webkit-box-sizing: border-box; }

html, body {
  background: #f5f6f7;
  width: 100%;
  height: 100%;
  overflow: hidden; }

.container {
  width: 100%;
  height: 100%;
  background: #fff;
  position: relative; }
  .container .header {
    width: 100%;
    height: 45px;
    line-height: 45px;
    background: #fff;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 3; }
    .container .header .hotel-back {
      position: absolute;
      left: 15px;
      color: #24aef2;
      z-index: 10; }
    .container .header h3 {
      position: absolute;
      left: 0;
      right: 0;
      text-align: center;
      line-height: 45px; }
  .container .hotel-date {
    position: fixed;
    left: 0;
    top: 45px;
    width: 100%;
    height: 38px;
    background: #eee;
    display: -webkit-flex;
    line-height: 38px; }
    .container .hotel-date p {
      flex: 1;
      text-align: center; }
      .container .hotel-date p span {
        margin-right: 7px; }
      .container .hotel-date p.recompose {
        color: #24aef2; }
        .container .hotel-date p.recompose span {
          padding-left: 40px; }
        .container .hotel-date p.recompose i {
          font-size: 12px; }
  .container .hotel-content {
    padding-top: 83px;
    padding-bottom: 38px;
    overflow: scroll;
    height: 100%; }

.hotel-list {
  width: 100%;
  height: 100px;
  border-bottom: 1px solid #ccc;
  display: -webkit-flex;
  padding: 10px; }
  .hotel-list h3 {
    padding: 0 !important; }
  .hotel-list dt {
    width: 70px;
    height: 70px;
    margin: 5px 10px 0 0; }
    .hotel-list dt img {
      min-width: 100%;
      height: 100%; }
  .hotel-list dd {
    flex: 1;
    width: 0; }
    .hotel-list dd p:nth-child(2) span:first-child label {
      color: orange;
      margin-right: 7px; }
    .hotel-list dd p:nth-child(2) span:first-child b {
      display: inline-block;
      width: 15px;
      height: 15px;
      color: #fff;
      text-align: center;
      line-height: 15px;
      font-size: 10px;
      font-weight: normal;
      background: orange; }
    .hotel-list dd p:nth-child(2) span:last-child {
      float: right; }
      .hotel-list dd p:nth-child(2) span:last-child b {
        color: orange;
        font-size: 16px;
        font-weight: normal;
        margin-right: 7px; }
    .hotel-list dd p:nth-child(3) i {
      margin-left: 3px;
      font-size: 10px; }
    .hotel-list dd p:nth-child(4) span:last-child {
      float: right; }
    .hotel-list dd p:nth-child(4) span:first-child {
      float: left;
      width: 80%;
      height: 20px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden; }

.order-page {
  position: absolute;
  width: 100%;
  background: #fff;
  left: 0;
  bottom: 0;
  z-index: 6; }
  .order-page dl {
    height: 100px;
    width: 100%;
    display: -webkit-flex;
    padding: 12px; }
    .order-page dl dt {
      width: 76px;
      height: 76px;
      margin-right: 12px; }
      .order-page dl dt img {
        width: 100%; }
    .order-page dl dd {
      line-height: 25px;
      flex: 1;
      position: relative; }
      .order-page dl dd p {
        color: orange; }
        .order-page dl dd p:nth-child(3) {
          width: 30px;
          height: 20px;
          background: orange;
          color: #fff;
          text-align: center;
          line-height: 20px; }
      .order-page dl dd div {
        position: absolute;
        top: 0px;
        right: 0px;
        font-size: 30px;
        color: #999;
        border-top: none; }
  .order-page div {
    border-top: 1px solid #eee;
    height: 40px;
    margin: 0 10px;
    line-height: 40px;
    color: #999;
    position: relative;
    padding-left: 20px; }
    .order-page div i:before {
      position: absolute;
      top: 5px;
      left: 0; }
    .order-page div span {
      float: right;
      color: #000; }
  .order-page .order-now {
    height: 50px;
    width: 100%;
    padding-left: 0;
    margin: 0;
    text-align: center;
    line-height: 50px;
    color: #fff;
    font-size: 20px;
    background: #24aef2; }

.none {
  display: none !important; }

.show {
  display: show !important; }

.loading {
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99; }

@keyframes loading {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(360deg); } }
  .loading .circle-wrap {
    width: 50px;
    height: 50px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-top: -25px;
    margin-left: -25px;
    animation: loading 3s linear infinite; }
    .loading .circle-wrap .circle {
      position: absolute;
      border-radius: 50%;
      width: 10px;
      height: 10px;
      background: #ccc; }
    .loading .circle-wrap .circle-index1 {
      transform: rotate(45deg);
      transform-origin: 25px 25px; }
    .loading .circle-wrap .circle-index2 {
      transform: rotate(90deg);
      transform-origin: 25px 25px; }
    .loading .circle-wrap .circle-index3 {
      transform: rotate(135deg);
      transform-origin: 25px 25px; }
    .loading .circle-wrap .circle-index4 {
      transform: rotate(180deg);
      transform-origin: 25px 25px; }
    .loading .circle-wrap .circle-index5 {
      transform: rotate(225deg);
      transform-origin: 25px 25px; }
    .loading .circle-wrap .circle-index6 {
      transform: rotate(270deg);
      transform-origin: 25px 25px; }
    .loading .circle-wrap .circle-index7 {
      transform: rotate(315deg);
      transform-origin: 25px 25px; }
    .loading .circle-wrap .circle-index8 {
      transform: rotate(360deg);
      transform-origin: 25px 25px; }

